import React, {useEffect, useState} from 'react';
import Menu from "../../menu/menuContainer";
import style from "./addAccident.module.css"
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';

const addAccident = (props) => {

    let maxId = 0;

    for (let i = 0; i < props.accidents.length; i++) {
        if (props.accidents[i].id > maxId) {
             maxId = props.accidents[i].id
        }
    }

    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [machine, setMachine] = useState(false);
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [partMachine, setPartMachine] = useState(false);
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [header, setHeader] = useState(false);
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [description, setDescription] = useState(false);
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [nku, setNku] = useState(false);

    let onMachineChange = (e) => {
        let valueMachine = e.currentTarget.value
        setMachine(valueMachine)
        setNku(props.listMachine.filter(machine => machine.number === valueMachine)[0].schema)
    }
    let onPartMachineChange = (e) => {
        setPartMachine(e.currentTarget.value)
    }
    let onHeaderChange = (e) => {
        setHeader(e.currentTarget.value)
    }
    let onDescriptionChange = (e) => {
        setDescription(e.currentTarget.value)
    }
    let onAddAccident = () => {
        let countDate = dateAccident.getDate() <= 9 ? "0" + dateAccident.getDate() : dateAccident.getDate();
        let monthDate = dateAccident.getMonth() <= 9 ? "0" + dateAccident.getMonth() : dateAccident.getMonth();
        let yearDate = dateAccident.getFullYear();
        let hoursDate = dateAccident.getHours() <= 9 ? "0" + dateAccident.getHours() : dateAccident.getHours();
        let minutesDate =  dateAccident.getMinutes() <= 9 ? "0" + dateAccident.getMinutes() : dateAccident.getMinutes();

        props.addAccidentToFireStore({id: maxId + 1,
            numberMachine: machine,
            nku: nku,
            clearDate: monthDate + "." + yearDate,
            part: partMachine,
            nameAccident: header,
            description: description,
            date: countDate + "." + monthDate
                + "." + yearDate + " –í—Ä–µ–º—è: " + hoursDate +":"+ minutesDate });
        props.history.push('/accidents')
    }

    // eslint-disable-next-line react-hooks/rules-of-hooks
        const [dateAccident, onChange] = useState(new Date());

    return <div className="container-lg center">
        <Menu/>
        <h1>–í–≤–µ–¥–∏—Ç–µ –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã–µ –¥–∞–Ω–Ω—ã–µ –∏ –æ–ø–∏—à–∏—Ç–µ –ø—Ä–æ–±–ª–µ–º—É</h1>
        <div className={style.formBlock}>
            <select onChange={onMachineChange} className="form-select" aria-label="Default select example">
                <option selected>–í—ã–±–µ—Ä–∏—Ç–µ –≠–ö–ì</option>
                {props.listMachine.map(m =>
                    <option value={m.number}>{m.number}</option>
                )}
            </select>
            <br/>

            <span className="input-group-text" id="basic-addon1">–£–∑–µ–ª</span>
            <input onChange={onPartMachineChange} type="text" className="form-control" placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —É–∑–ª–∞" aria-label="Username"
                   aria-describedby="basic-addon1"/>
            <br/>
            <span className="input-group-text" id="basic-addon1">–ó–∞–≥–æ–ª–æ–≤–æ–∫</span>
            <input onChange={onHeaderChange} type="text" className="form-control" placeholder="–í–≤–µ–¥–∏—Ç–µ –∑–∞–≥–æ–ª–æ–≤–æ–∫" aria-label="Username"
                   aria-describedby="basic-addon1"/>
            <br/>
        </div>
        <div className={style.formBlock}>
            <Calendar onChange={onChange} value={dateAccident} />
        </div>

        <div className="input-group">
            <span className="input-group-text">–û–ø–∏—Å–∞–Ω–∏–µ</span>
            <textarea onChange={onDescriptionChange} className="form-control" aria-label="With textarea"></textarea>
        </div>
        <br/>

        <button onClick={onAddAccident} type="button" className="btn btn-outline-primary">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>

    </div>
}

export default addAccident;